'use client';

import { useCart } from '@/context/CartContext';
import { X, Plus, Minus, Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/Button';
import Link from 'next/link';

export const CartDrawer = () => {
    const { items, isDrawerOpen, toggleDrawer, updateQuantity, removeItem, cartTotal } = useCart();

    if (!isDrawerOpen) return null;

    return (
        <div className="fixed inset-0 z-[1000] flex justify-end">
            {/* Backdrop */}
            <div
                className="absolute inset-0 bg-black/50 backdrop-blur-sm transition-opacity"
                onClick={toggleDrawer}
            />

            {/* Drawer */}
            <div className="relative w-full max-w-md bg-white h-full shadow-2xl flex flex-col animate-in slide-in-from-right duration-300">
                <div className="flex items-center justify-between p-6 border-b">
                    <h2 className="text-xl font-bold">Your Cart</h2>
                    <button onClick={toggleDrawer} className="p-2 hover:bg-gray-100 rounded-full">
                        <X size={24} />
                    </button>
                </div>

                <div className="flex-1 overflow-y-auto p-6">
                    {items.length === 0 ? (
                        <div className="text-center py-20 text-gray-500">
                            Your cart is empty. <br />
                            <button
                                className="text-black font-bold mt-4 underline"
                                onClick={toggleDrawer}
                            >
                                Continue Shopping
                            </button>
                        </div>
                    ) : (
                        <div className="flex flex-col gap-6">
                            {items.map((item) => (
                                <div key={item.id} className="flex gap-4">
                                    <div className="w-20 h-20 bg-gray-100 rounded-lg overflow-hidden shrink-0">
                                        <img src={item.image} alt={item.name} className="w-full h-full object-cover" />
                                    </div>
                                    <div className="flex-1">
                                        <div className="flex justify-between items-start">
                                            <h3 className="font-medium text-sm line-clamp-2">{item.name}</h3>
                                            <button
                                                onClick={() => removeItem(item.id)}
                                                className="text-gray-400 hover:text-red-500"
                                            >
                                                <Trash2 size={16} />
                                            </button>
                                        </div>
                                        <p className="font-bold mt-1">${item.price}</p>
                                        <div className="flex items-center gap-3 mt-3">
                                            <button
                                                className="w-8 h-8 flex items-center justify-center border rounded-full hover:bg-gray-50"
                                                onClick={() => updateQuantity(item.id, item.quantity - 1)}
                                            >
                                                <Minus size={14} />
                                            </button>
                                            <span className="text-sm font-medium w-4 text-center">{item.quantity}</span>
                                            <button
                                                className="w-8 h-8 flex items-center justify-center border rounded-full hover:bg-gray-50"
                                                onClick={() => updateQuantity(item.id, item.quantity + 1)}
                                            >
                                                <Plus size={14} />
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                </div>

                {items.length > 0 && (
                    <div className="p-6 border-t bg-gray-50">
                        <div className="flex justify-between items-center mb-4 text-lg font-bold">
                            <span>Total</span>
                            <span>${cartTotal.toFixed(2)}</span>
                        </div>
                        <Link href="/checkout" onClick={toggleDrawer}>
                            <Button className="w-full h-12 text-lg">Checkout</Button>
                        </Link>
                    </div>
                )}
            </div>
        </div>
    );
};
